<template>
    <div class="defaultTextarea">
        <Input v-model="modelData.value" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..." />
        <Alert type="warning" v-show="errors.has(modelData.help)" style="margin-top: .5rem">{{ errors.first(modelData.help) }}</Alert>
    </div>
</template>

<script>
    export default {
        name: "mod-textarea",
        props:["modelData"],
        inject: ['$validator'],
    }
</script>

<style scoped>
    .defaultTextarea{
        margin: .2rem 0;
    }
</style>