<template>
    <div class="header-menu">
        <div class="Menu">
            <template v-if="categories">
                <Menu mode="horizontal" ref="categories_menu" :theme="menuTheme" :active-name="this.$store.state.menu.active.parent" width="auto"
                      @on-select="selectMenu">
                    <MenuItem v-for="(item,index) in categories" v-bind:name="item.id"  :key="index">
                        <Avatar :src="$store.state.IMAGES+'/uploads/' + item.image" class="avatar"/>
                    </MenuItem>
                </Menu>
            </template>
        </div>
        <template v-if="hasLoading">
            <AppSubMenu :subID="activeMenu"></AppSubMenu>
        </template>
    </div>
</template>
<script>
    import AppSubMenu from "./app-subMenu";
    export default {
        name: "app-Menu",
        components: {AppSubMenu},
        data: () => ({
            menuTheme: 'light',
            categories: false,
            activeMenu: 4,
            hasLoading:false
        }),
        created() {
            this.$Message.loading(this.$i18n.t('loading'));
            this.loadData();
        },
        methods: {
            async loadData(){
                await this.axios.get(this.$store.state.API+'web/categories/find/all').then(function (res) {
                    if (res.data.status_code === 200) {
                        this.categories = res.data.data;
                        let temp = this.$store.state.menu;
                        temp.data = res.data.data;
                        this.$store.commit('menu',temp);
                        console.log("查询数据");
                        this.hasLoading = true;
                    } else {
                        this.$Message.error(this.$i18n.t('error.servers'));
                    }
                }.bind(this)).catch(function () {
                    this.$Message.error(this.$i18n.t('error.servers'));
                }.bind(this));
            },
            selectMenu(name) {
                let temp = this.$store.state.menu;
                temp.active.parent = this.$refs.categories_menu.currentActiveName;
                console.log("菜单改选中",temp.data);
                for (var item in temp.data) {
                    if (temp.data[item].id === name){
                        this.$store.commit("changSubMenu", temp.data[item].children[0].id);
                        break;
                    }
                }
                this.$store.commit('menu',temp);
                this.activeMenu = this.$refs.categories_menu.currentActiveName;
                //this.$store.commit("changSubMenu",name);
                console.log("菜单改选",this.$refs.categories_menu.currentActiveName);
            },
        }
    }
</script>

<style scoped>
    .Menu {
        margin-top: 2rem;
        margin-bottom: 2rem;
        padding: 1rem 20%;
    }

    .avatar {
        width: 64px;
        height: 64px;
    }

    @media (max-width: 992px ) {
        .Menu {
            padding: 1rem;
        }

        .avatar {
            width: 48px;
            height: 48px;
        }
    }

    @media (max-width: 500px) {
        .Menu {
            margin-top: 1rem;
            margin-bottom: 1rem;
            padding: 1rem;
        }

        .ivu-menu-horizontal .ivu-menu-item {
            padding: 6px;
        }
    }

    .Menu ul {
        background: transparent;
        display: flex;
        justify-content: space-between;
    }

    .Menu ul:after {
        background: transparent !important;
    }

    .Menu ul li {
        border-bottom: 0 !important;
        height: 64px !important;
    }
    .ivu-avatar{

        border: 5px solid white !important;
        border-radius: 50%;
    }
    .ivu-menu-item-active .ivu-avatar {
        box-shadow: 0 0 10px rgba(8, 8, 8, 0.40);
    }
</style>