<template>
    <div class="product">
        <template v-if="pData!=false">
            <div class="header">
                <h1>{{ $t('shop.pay.cashier') }}</h1>
                <span>Cashier</span>
            </div>
            <div class="container">
                <div class="info">
                    <Card :title="$t('shop.pay.titleDes')" icon="ios-ios-analytics" shadow>
                        <CellGroup>
                            <Cell :title="pData.name" :label="pData.description" disabled/>

                            <Cell :title="$t('balance')" disabled>
                                <Badge :text="$t('curr',[pData.price])" slot="extra"/>
                            </Cell>
                        </CellGroup>
                    </Card>
                </div>
                <div class="form">
                    <div class="email">
                        <Input v-model="email" prefix="ios-mail-outline" :placeholder="$t('val.email')" name="email"
                               style="width: 100%" size="large" v-validate="'required|email'"/>

                        <Alert type="warning" v-show="errors.has('email')" style="margin-top: .5rem">
                            {{errors.first('email') }}
                        </Alert>
                    </div>
                    <div class="pinfo" v-if="pData.data.length !== 0">
                        <h6>{{ $t('shop.pay.youcan')}}</h6>
                        <div style="padding-left: .4rem">
                            <template v-for="(item,index) in pData.data">
                                <template v-for="(jtem) in item.data">
                                    <template v-if="!jtem.private">
                                        <template v-if="jtem.model === '101'">
                                            <mod-input :modelData="jtem"></mod-input>
                                        </template>
                                        <template v-else-if="jtem.model === '102'">
                                            <mod-textarea :modelData="jtem"></mod-textarea>
                                        </template>
                                        <template v-else-if="jtem.model === '103'">
                                            <mod-ins-auto-media :modelData="jtem"></mod-ins-auto-media>
                                        </template>
                                    </template>
                                </template>
                            </template>
                        </div>
                    </div>
                </div>
            </div>
            <div class="footer">
                <Row>
                    <Col :xs="24" :sm="24" :md="12" :lg="12" class="a1" style="text-align: center;">
                        <RadioGroup ref="payment" v-model="payment" type="button" size="large"
                                    @on-change="changPayModel">
                            <Radio v-for="item in paymentList" :label="$t('shop.pay.model.'+item)" :key="item"></Radio>
                        </RadioGroup>
                    </Col>
                    <Col :xs="24" :sm="24" :md="12" :lg="12" class="a2">
                        <Button type="success" shape="circle" icon="ios-checkmark" style="width: 100%"
                                v-on:click="subButton">{{$t('shop.pay.checkTrue') }}
                        </Button>
                    </Col>
                </Row>
            </div>
        </template>
        <Modal
                v-model="payModel"
                :title="this.$i18n.t('shop.pay.title')"
                :loading="payModelLoading"
                @on-ok="asyncOK" style="text-align: center">
            <qrcode :value="payLink.link"></qrcode>
            <Divider>Or</Divider>
            <Button type="success" style="width: 100%">{{ $t('shop.pay.checkTrue')}}</Button>
        </Modal>
    </div>
</template>

<script>
    import Qrcode from "@chenfengyuan/vue-qrcode/src";
    import ModInput from "./mod/mod-input";
    import ModTextarea from "./mod/mod-textarea";
    import ModInsAutoMedia from "./mod/mod-ins-auto-media";

    export default {
        name: "product",
        components: {ModInsAutoMedia, ModTextarea, ModInput, Qrcode},
        props: ['pid'],
        data: () => ({
            payModel: false,
            payModelLoading: true,
            payLink: {
                link: null,
                pid: null,
            },
            pData: false,
            payment: "paypal",
            paymentList: ["alipay", "wepay", "paypal"],
            pay: "paypal",
            email: null,
            pushData: []
        }),
        created() {
            this.loadProductData ();
        },
        mounted() {

        },
        provide() {
            return {
                $validator: this.$validator,
            }
        },
        methods: {
            async loadProductData() {
                await this.axios.get ( this.$store.state.API + 'web/product/find/id/' + this.pid ).then ( function (res) {
                    if (res.data.status_code === 200) {
                        this.pData = res.data.data;
                        this.pData.description = this.pData.description.split ( '\r\n' );
                        let t = "";
                        for (let descriptionKey in this.pData.description) {
                            t += descriptionKey.replace ( /<[^>]*>/g ) + " ";
                        }
                        this.pData.description = t;
                    } else {
                        this.$Message.error ( this.$i18n.t ( 'error.servers' ) );
                    }
                }.bind ( this ) ).catch ( function () {
                    this.$Message.error ( this.$i18n.t ( 'error.servers' ) );
                }.bind ( this ) );
            },
            changPayModel() {
                let _this = this;
                this.$refs.payment.childrens.forEach ( function (value, index) {
                    if (value.currentValue) {
                        _this.pay = _this.paymentList[index];
                    }
                } )
            },
            subButton() {
                var validationArray = this.$children.map ( function (child) {
                    return child.$validator.validateAll ();
                } );

                window.Promise.all ( validationArray ).then ( (v) => {
                    v.some ( element => {
                        if (element === false) {
                            throw "exists error in child component";
                        }
                    } );
                    this.$Message.success ( this.$i18n.t ( 'val.pass' ) );
                    this.createPayment ();
                    return null;
                } ).catch ( () => {
                    this.$Message.warning ( this.$i18n.t ( 'val.nopass' ) );
                } );
            },
            async createPayment() {
                let _this = this;
                _this.pushData = [];
                _this.pData.data.forEach ( function (item, iindex) {
                    console.log ( item, iindex );
                    let t = {};
                    t.service = item.service;
                    t.supService = item.supplier;
                    item.data.forEach ( function (jtem, jindex) {
                        console.log ( jtem, jindex );
                        let name = jtem.name;
                        let value = jtem.value;
                        t[name] = value;
                    } );
                    _this.pushData.push ( t );
                } );
                await this.axios.post ( this.$store.state.API + 'web/pay', {
                    mail: _this.email,
                    data: _this.pushData,
                    payment: _this.pay,
                    pid: _this.pData.id
                } ).then ( function (response) {
                    if (response.data.status_code === 200) {
                        if (response.data.data.pid != null) {
                            _this.payLink = response.data.data;
                            _this.payModel = true;
                        } else {
                            this.$Message.error ( this.$i18n.t ( 'error.servers' ) );
                        }
                    } else {
                        this.$Message.error ( this.$i18n.t ( 'error.servers' ) );
                    }
                }.bind ( this ) ).catch
                ( function () {
                    this.$Message.error ( this.$i18n.t ( 'error.servers' ) );
                }.bind ( this ) );
            },
            asyncOK() {
                console.log ( "1" );
            }
        }
    }
</script>

<style scoped>
    .product {
        padding: 20px;
        background: #fff;
        border-radius: 12px;
        margin: 2rem 0;
    }

    .product .header {
        padding: .5rem 0;
    }

    .product .container .form {
        padding: 1rem;
        background: #eee;
        border-radius: 4px;
        margin: .5rem 0;
    }

    .product .container .form .pinfo {
        margin-top: .5rem;
        font-size: 14px;
        padding: .2rem 0 .2rem .5rem;
    }

    .product .footer {
        margin-top: 1rem;
        border-top: 1px solid #eeeeee;
        padding-top: 1rem;
    }

    @media (max-width: 767px) {
        .product {
            border-radius: 0;
            margin: 0;
            padding-top: 2rem;
        }

        .a2 {
            margin-top: .5rem;
            text-align: center;
        }
    }

    @media screen and (min-width: 767px) and (max-width: 768px) {
        .product {
            padding-top: 2rem;
        }
    }

    @media (max-width: 330px) {
        .footer .ivu-radio-group-button {
            padding: 0 .5rem;
        }
    }

    @media (max-width: 768px) {

        .footer .ivu-radio-group-button {
            display: flex;
            justify-content: space-between;
            padding: 0 2rem;
        }

        .footer .ivu-radio-group-button label {
            width: 100%;
        }
    }

    @media (min-width: 768px) {
        .ivu-col-span-sm-24 {
            padding-top: .5rem;
        }

        .footer .ivu-radio-group-button {
            display: flex;
            justify-content: space-between;
            padding: 0 2rem;
        }

        .footer .ivu-radio-group-button label {
            width: 100%;
        }
    }
</style>