import home from './components/home'
import shop from './components/shop'
import product from './components/shop/product'

export default [
    {
        path: '/',
        redirect: '/shop'
    },
    {
        path: '/home',
        component: home,
        redirect: '/shop'
    },
    {
        path: '/shop',
        component: shop
    },
    {
        path: '/shop/:pid',
        name: 'product',
        component: product,
        props: true
    }
]