import Vue from 'vue';
import 'es6-promise/auto';
import Vuex from 'vuex';
import iView from 'iview';
import VueI18n from 'vue-i18n';
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueRouter from 'vue-router';
import routes from './routers';
import VeeValidate from 'vee-validate';
import './assets/css/normalize.css';
import 'iview/dist/styles/iview.css';
import zh_CN from 'vee-validate/dist/locale/zh_CN';
import zh_TW from 'vee-validate/dist/locale/zh_TW';
import ja from 'vee-validate/dist/locale/ja';
import en from 'vee-validate/dist/locale/en';
import VueQrcode from '@chenfengyuan/vue-qrcode';
Vue.component(VueQrcode.name, VueQrcode);
// 组件
import App from './App'

// 导入扩展
Vue.use(Vuex);
Vue.use(VueI18n);
Vue.use(VueRouter);
Vue.use(VueAxios, axios);
Vue.use(iView);

// 环境提示
Vue.config.productionTip = false;

// 路由
const router = new VueRouter({
    routes
});

// 多语言
const DEFAULT_LANG = 'zh_CN';
const LOCALE_KEY = 'localeLanguage';

const locales = {
    'zh_CN': require('./languages/zh-cn.json'),
    'zh_TW': require('./languages/zh-tw.json'),
    en: require('./languages/en.json'),
    ja: require('./languages/ja.json'),
};

const i18n = new VueI18n({
    locale: DEFAULT_LANG,
    messages: locales,
    silentTranslationWarn:false
});

Vue.use(VeeValidate, {
    i18nRootKey: 'validations', // customize the root path for validation messages.
    i18n,
    dictionary: {
        zh_CN,
        zh_TW,
        en,
        ja
    }
});

export const setup = lang => {
    if (lang === undefined) {
        lang = window.localStorage.getItem(LOCALE_KEY);
        if (locales[lang] === undefined) {
            lang = DEFAULT_LANG
        }
    }
    window.localStorage.setItem(LOCALE_KEY, lang);

    document.body.setAttribute('lang', lang);
    document.getElementsByTagName('html')[0].setAttribute('lang', lang);
    /** @namespace Vue.config */
    Vue.config.lang = lang;
    i18n.locale = lang
};

setup();

//vuex

const store = new Vuex.Store({
    state: {
        menu:{
            active:{
                parent:4,
                children:8,
            },
            data:null
        },
        product:null,
        activeProduct:null,
        IMAGES:'http://foliker.demo.hi.cn/',
        API:'http://foliker.demo.hi.cn/api/'
    },
    mutations: {
        menu(state,payload){
            state.menu = payload;
        },
        changSubMenu(state,payload){
            state.menu .active.children = payload;
        }
    }
});

// 实例
new Vue({
    i18n,
    store,
    render: h => h(App),
    router
}).$mount('#app');