<template>
    <div class="modeInsAutoMedia">
        <Tabs>
            <TabPane label="自动完成" icon="ios-git-compare">
                <Input v-model="username" size="large" placeholder="large size" style="width: 100%" v-validate="'required'" :name="modelData.help">
                    <span slot="prepend">ins 账号</span>
                </Input>
								<Alert type="warning" v-show="errors.has(modelData.help)" style="margin-top: .5rem">{{
										errors.first(modelData.help) }}
								</Alert>
                <template v-if="medias">
                    <Row class="loadingbox">
											<Col span="24" v-for="(item, index) in medias.data.medias" :key="index" @click.native="pushlink(item.link,index)">
												<Card class="card" :class="{ active:index==current}">
													<div class="images">
														<img :alt="item.link" :src="item.imageThumbnailUrl" style="width: 100%;border-radius: 4px">
														</div>
														<div class="texts">
															<h3>{{ item.caption.length === 0 ?"None":item.caption }}</h3>
															<a :href="item.link" target="_blank">{{ item.link }}</a>
														</div>
												</Card>
											</Col>
											<infinite-loading :identifier="username" @infinite="infiniteHandler">
												<span slot="no-more">
													我们是有底线的
												</span>
											</infinite-loading>
                    </Row>
                </template>
            </TabPane>
            <TabPane label="手动输入" icon="ios-hand">
                <Input v-model="modelData.value" size="large" placeholder="large size" style="width: 100%"
                       v-validate="'required'" :name="modelData.help">
                    <span slot="prepend">{{ modelData.help }}</span>
                </Input>
                <Alert type="warning" v-show="errors.has(modelData.help)" style="margin-top: .5rem">{{
                    errors.first(modelData.help) }}
                </Alert>
            </TabPane>
        </Tabs>
    </div>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading';

export default {
    name: 'mod-ins-auto-media',
    data: () => ({
        username: null,
        medias: false,
        source: null,
        current: -1
    }),
    props: ['modelData'],
    inject: ['$validator'],
    components: {
        InfiniteLoading
    },
    watch: {
        username: function(nValue, oValue) {
            this.medias = false;
            this.getInfo(false, null);
        }
    },
    methods: {
        cancelQuest() {
            if (typeof this.source === 'function') {
                this.source('终止请求'); //取消请求
            }
        },
        getInfo(isScroll, $state) {
            console.log('Loading...');
            let _this = this;
            this.cancelQuest();

            let link = this.$store.state.API + 'auto/inst/media?userName=' + this.username;
            if (isScroll) {
                if (this.medias.data.hasNextPage) {
                    link = link + '&nextID=' + this.medias.data.maxId;
                    this.$Message.loading('加载更多数据中...');
                } else {
                    this.$Message.warning('没有更多数据了...');
                }
            }
            console.log('AutoInsLink', link);
            if (this.username.length > 1) {
                this.axios
                    .get(link, {
                        cancelToken: new this.axios.CancelToken(function executor(c) {
                            _this.source = c;
                        })
                    })
                    .then(
                        function(resposen) {
                            resposen = resposen.data;
                            console.log('加载到的数据:', resposen);
                            if (resposen.status_code === 200) {
                                if (isScroll) {
                                    if (resposen.data.medias > 28) {
                                        this.medias.data.medias.push(...resposen.data.medias);
                                        this.medias.data.maxId = resposen.data.maxId;
                                        this.medias.data.hasNextPage = resposen.data.hasNextPage;
                                        this.$Message.success('更多数据加载完成...');
                                        $state.loaded();
                                    } else {
                                        $state.complete();
                                    }
                                } else {
                                    this.medias = resposen;
                                }
                                this.$Message.success('加载完成请选择你的数据...');
                            } else {
                                this.$Message.error('A1,服务器出现了点小问题,请使用手动输入...');
                            }
                            if ($state !== null) {
                                $state.complete();
                            }
                        }.bind(this)
                    )
                    .catch(
                        function(error) {
                            if (this.axios.isCancel(error)) {
                                console.log('Rquest canceled', error.message);
                            } else {
                                this.$Message.error(
                                    '用户名不存在或者输入错误请重试或者使用手动输入...'
                                );
                                console.log(error);
                            }
                        }.bind(this)
                    );
            }
        },
        infiniteHandler($state) {
            setTimeout(() => {
                console.log('延迟加载');
                this.getInfo(true, $state);
            }, 300);
        },
        pushlink(link, index) {
            this.modelData.value = link;
            this.current = index;
        }
    }
};
</script>
<style scoped>
.modeInsAutoMedia {
    margin: 0.2rem 0;
}

.images {
    width: 40px;
    height: 40px;
    display: inline-block;
}

.texts {
    display: inline-block;
    float: right;
    width: calc(100% - 40px);
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 4px;
}
.loadingbox {
    overflow: auto;
    height: 300px;
    margin-top: 6px;
    padding-right: 2px;
}
.card {
    width: 100%;
		cursor: pointer;
    margin-bottom: 6px;
}
.card.active {
    background: #2c8cf0;
    color: #fff;
}
.card.active a {
    color: #fff;
}
</style>
