<template>
    <div class="app-product">
        <template v-if="productList!=false">
            <div class="container">
                <Row type="flex" justify="start">
                    <Col :xs="24" :sm="12" :md="12" :lg="8" class="product" v-for="(item,index) in productList" :key="index">
                        <Card shadow>
                            <p slot="title">{{ item.name }}</p>
                            <div class="ntr" v-html="item.description"></div>
                            <div class="footer">
                                <ButtonGroup vertical>
                                    <Tooltip :content="$t('curr',['$'])" placement="top">
                                        <Button type="text">{{ item.price }}</Button>
                                    </Tooltip>
                                    <Button size="large" long type="primary"
                                            :to="{name:'product',params: { pid: item.id}}">{{ $t('shop.buy') }}
                                    </Button>
                                </ButtonGroup>
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>
        </template>
        <template v-else>
            <div class="flexCenter">
                <p>{{ $t('noneProduct') }}</p>
            </div>
        </template>
    </div>
</template>

<script>
    export default {
        name: "app-product",
        data: () => ({
            productList: false
        }),
        created: function () {
            this.loadProductData(this.$store.state.menu.active.children)
        },
        computed: {
            ListenVuexSubMenu() {
                return this.$store.state.menu.active.children;
            }
        },
        watch: {
            ListenVuexSubMenu(value) {
                this.$Message.loading(this.$i18n.t('loading'));
                this.loadProductData(value);
            }
        },
        methods: {
            async loadProductData(id) {
                await this.axios.get(this.$store.state.API+'web/categories/find/id/' + id).then(function (res) {
                    if (res.data.status_code === 200) {
                        if (res.data.data.length != 0) {
                            this.productList = res.data.data;
                        } else {
                            this.productList = false;
                        }
                    } else {
                        this.$Message.error(this.$i18n.t('error.servers'));
                    }
                }.bind(this)).catch(function () {
                    this.$Message.error(this.$i18n.t('error.servers'));
                }.bind(this));
            }
        }
    }
</script>

<style scoped>
    .app-product {
        background: rgba(0, 0, 0, 0.1);
        padding: 20px;
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
        min-height: 50vh;
    }

    .app-product .flexCenter {
        align-items: center;
        display: flex;
        justify-content: center;
        height: 50vh;
    }

    .app-product .container {
        min-height: 50vh;
    }

    .app-product .flexCenter p {
        font-size: 2rem;
        font-weight: 100;
        letter-spacing: 4px;
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif;
        color: #d6d6d6;
        text-shadow: 0 0 1px #000;
    }

    .app-product .product {
        padding: 12px;
    }

    .app-product .product .ivu-card-head p {
        text-align: center;
        font-weight: 400;
        letter-spacing: 2px;
    }

    .app-product .product .footer {
        border-radius: 2px;
        margin: 6px 0 0;
        padding: 6px 6px 0;
    }

    .app-product .product .footer .ivu-btn-group-vertical {
        width: 100%;
        border-radius: 4px;
        border: 1px solid rgba(0, 0, 0, 0.17);
        z-index: 900;
        border-bottom: 0;
    }

    .app-product .product .footer .ivu-btn-group-vertical > .ivu-btn:first-child:not(:last-child) {
        cursor: default !important;
    }

    .app-product .product .footer .ivu-tooltip,
    .app-product .product .footer .ivu-tooltip button {
        width: 100%;
    }

    .app-product .product .footer .ivu-btn-group-vertical > .ivu-btn:last-child:not(:first-child) {
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
    }

    @media (max-width: 768px) {
        .app-product {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
</style>