<template>
    <div class="subMenu">
        <Menu mode="horizontal" ref="subMenu" theme="light" :active-name="this.$store.state.menu.active.children" @on-select="changSubMenuChildren">
            <template v-if="nowData != null">
                <template v-if="nowData.length > 4">
                    <template v-for="(item,index) in nowData">
                        <template v-if="index < 3">
                            <MenuItem :name="item.id">
                                {{ item.title }}
                            </MenuItem>
                        </template>
                    </template>
                    <Submenu name="3">
                        <template slot="title">
                            {{ $t('more') }}
                        </template>
                        <MenuGroup :title="this.$i18n.t('more')">
                            <template v-for="(item,index) in nowData">
                                <template v-if="index > 2">
                                    <MenuItem :name="item.id">
                                        {{ item.title }}
                                    </MenuItem>
                                </template>
                            </template>
                        </MenuGroup>
                    </Submenu>
                </template>
                <template v-else>
                    <template v-for="(item,index) in nowData">
                        <MenuItem :name="item.id">
                            {{ item.title }}
                        </MenuItem>
                    </template>
                </template>
            </template>
        </Menu>
    </div>
</template>

<script>
    export default {
        name: "app-subMenu",
        props: ["subID"],
        data: () => ({
            nowData: null,
        }),
        created: function () {
            console.log("初始化,查询子菜单数据",this.$store.state.menu.data);
            var a = this.$store.state.menu.active.parent, b = this.$store.state.menu.data;
            for (var item in b) {
                if (b[item].id === a){
                    this.nowData = b[item].children;
                    break;
                }
            }
            this.$nextTick(()=>{
                this.$refs.subMenu.updateActiveName();
            })
        },
        watch: {
            'subID': function() {
                console.log("更改中,查询子菜单数据",this.$store.state.menu.data);
                var a = this.$store.state.menu.active.parent, b = this.$store.state.menu.data;
                for (var item in b) {
                    if (b[item].id === a){
                        this.nowData = b[item].children;
                        break;
                    }
                }

                this.$nextTick(()=>{
                    this.$refs.subMenu.updateActiveName();
                })
            }
        },
        methods:{
            changSubMenuChildren(name){
                this.$store.commit("changSubMenu",name)
            }
        }
    }
</script>

<style scoped>
    .subMenu ul{
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
        display: flex;
        justify-content: space-between!important;
    }
    .subMenu ul .ivu-menu-item,
    .subMenu ul .ivu-menu-submenu{
        width: 100%;
        text-align: center;
    }
    .subMenu ul .ivu-menu-submenu-title{
        text-align: center;
    }
    .subMenu .ivu-menu-item{
        padding: 0 2%;
    }
    .ivu-menu-submenu-title i{}
    @media (max-width: 500px) {
        .ivu-menu-submenu-title i{
            display: none;
        }
    }
    @media (max-width: 768px){
        .subMenu ul {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }
    }
</style>