<template>
    <div class="defaultInput">
        <Input v-model="modelData.value" size="large" placeholder="large size" style="width: 100%" v-validate="'required'" :name="modelData.help">
            <span slot="prepend">{{ modelData.help }}</span>
        </Input>
        <Alert type="warning" v-show="errors.has(modelData.help)" style="margin-top: .5rem">{{ errors.first(modelData.help) }}</Alert>
    </div>
</template>

<script>
    export default {
        name: "mod-input",
        props:["modelData"],
        inject: ['$validator'],
    }
</script>

<style scoped>

    .defaultInput{
        margin: .2rem 0;
    }
</style>