<template>
    <Row class="te">
        <Col :xs="{ span: 24, offset: 0 }" :sm="{ span: 20, offset: 2 }" :md="{ span: 18, offset: 3 }"
             :lg="{ span: 16, offset: 4 }">
            <appHeader/>
            <div class="appContainer">
                <router-view/>
            </div>
        </col>
        <div class="tools">
            <ButtonGroup :vertical="screenWidth >= 766">
                <Button icon="ios-arrow-back" v-on:click="goBack" v-if="goBackButton">{{ $t('goBack') }}</Button>
                <Button icon="ios-compass" v-on:click="changeLang">{{ $t('lang') }}</Button>
                <Button icon="ios-contacts">{{ $t('help') }}</Button>
            </ButtonGroup>
        </div>
    </Row>
</template>
<script>
    import appHeader from "./components/layout/app-header";

    export default {
        name: "App",
        components: {
            appHeader
        },
        data: () => ({
            goBackButton: false,
            hasLoading: true,
            screenWidth: document.body.clientWidth
        }),
        watch: {
            '$route'() {
                window.history.length > 1 ? this.goBackButton = true : this.goBackButton = false;
            },
            screenWidth(val) {
                if (!this.timer) {
                    this.screenWidth = val;
                    this.timer = true;
                    let that = this;
                    setTimeout(function () {
                        that.timer = false
                    }, 400)
                }
            }
        },
        mounted () {
            const that = this
            window.onresize = () => {
                return (() => {
                    window.screenWidth = document.body.clientWidth
                    that.screenWidth = window.screenWidth
                })()
            }
        },
        methods: {
            goBack() {
                window.history.length > 1
                    ? this.$router.go(-1)
                    : this.$router.push('/')
            },
            changeLang() {
                const keys = Object.keys(this.$i18n.messages);
                for (let i = 0; i < keys.length; i++) {
                    const iKey = keys[i];
                    if (iKey === this.$i18n.locale) {
                        if (i + 1 === keys.length) {
                            const iKey = keys[0];
                            this.$i18n.locale = iKey;
                            window.localStorage.setItem('localeLanguage', iKey);
                            document.body.setAttribute('lang', iKey);
                            document.getElementsByTagName('html')[0].setAttribute('lang', iKey);
                            break;
                        } else {
                            const iKey = keys[i + 1];
                            this.$i18n.locale = iKey;
                            window.localStorage.setItem('localeLanguage', iKey);
                            document.body.setAttribute('lang', iKey);
                            document.getElementsByTagName('html')[0].setAttribute('lang', iKey);
                            break;
                        }
                    }
                }
            }
        }
    }
</script>

<style scoped>
    body {
        background-color: aqua;
    }

    .tools {
        position: fixed;
        bottom: 0;
        right: 0;
        margin: 1rem;
    }


    @media (max-width: 768px) {

        .tools {
            margin: 0;
            left: 0;
            right: 0;
            height: 50px;
        }

        .tools .ivu-btn-group {
            display: flex;
            justify-content: space-between;
        }
        .tools .ivu-btn-group button{
            width: 100%;
            height: 50px;
            border-radius: 0;
            border: 0;
            border-top: 1px solid rgba(0, 0, 0, 0.3);
        }
        .tools .ivu-btn-group button:last-child{
            border-left: 1px solid rgba(0, 0, 0, 0.3);
        }
        .te{
            margin-bottom: 50px;
        }
    }
</style>